/* eslint-disable no-nested-ternary */
import React, { useEffect } from 'react'
import { Typography, Button } from '@material-ui/core'
import { Box } from '@mui/material'
import { useNavigate } from 'react-router-dom'
import useStyles from './Filters.style'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'
import TextfieldSearch from './textfield-search/textfield-search'
import { ControlledSelectField } from '../FormElements'
import { useAppSelector } from '../../../services/store/hooks'
import { PROJECT_START_DATE } from '../../../utils/constants'

function CompaniesFilters({
  searchString,
  handleSearchChange,
  dateProps,
  errors,
  control,
  multipleCoursesProps,
  statusProps,
  handleAddVacancy,
  setSearchString,
  setSelectedCourses,
  setStatus,
}) {
  const classes = useStyles()
  const navigate = useNavigate()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { coursesOptions, selectedCourses, handleChangeMultipleCourses } = multipleCoursesProps
  const { statusId, statuses, handleChangeStatus } = statusProps

  const { isLockUpdate, filters } = useAppSelector((store) => store.companiesPage)

  const handleDownload = () => navigate('/vacancies/export-page', { state: { companies: true } })

  //       
  useEffect(() => {
    if (isLockUpdate) {
      handleDateIntervalStartChange(new Date(filters?.start_date || PROJECT_START_DATE))
      handleDateIntervalEndChange(new Date(filters?.end_date || Date.now()))
      setSearchString(filters?.search)
      setSelectedCourses(filters?.courses)
      setStatus(filters?.status ? { id: filters?.status, name: filters?.status } : { id: 0, name: ' ' })
    }
  }, [])

  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
               
            </Typography>
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
          </div>
          <Box sx={{ gap: '20px' }} className={classes.filters__buttonsContainer}>
            <Button
              className={`${classes.button} ${classes.button_color_white} ${classes.button_type_small}`}
              onClick={handleDownload}
            >
               
            </Button>{' '}
            <Button className={`${classes.button} ${classes.button_type_middle}`} onClick={handleAddVacancy}>
               
            </Button>
          </Box>
        </div>

        <div className={classes.filters__container} style={{ alignItems: 'flex-start' }}>
          <div className={classes.filters} style={{ alignItems: 'flex-start' }}>
            <TextfieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder=" "
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
              isLong
            />
            <ControlledSelectField
              tipContent=" "
              tipBlack
              value={selectedCourses || []}
              onChange={handleChangeMultipleCourses}
              items={coursesOptions || []}
              displayEmpty
              multiple
              maxWidth={288}
              selectAll=" "
            />
            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={190}
            />
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(CompaniesFilters)
