/* eslint-disable no-nested-ternary */
import React from 'react'
import { Box } from '@mui/material'
import useStyles from './Filters.style'
import TextfieldSelect from './textfield-select/textfield-select'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import TextfieldSearch from './textfield-search/textfield-search'

function AboutCompanySecondTabFilters({
  searchString,
  control,
  courseProps,
  cohortsProps,
  statusProps,
  handleSearchChange,
  errors,
}) {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const { courseId, courses, handleChangeCourse } = courseProps
  const { cohortId, cohorts, handleChangeCohort, isCohortDisabled } = cohortsProps
  const { statusId, statuses, handleChangeStatus } = statusProps

  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.filters__container}>
          <Box sx={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
            <TextfieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder=" "
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
              isMedium
            />
          </Box>
          <Box ml="16px" className={classes.filters__container}>
            <Box sx={{ display: 'flex', gap: '12px' }}>
              <TextfieldSelect
                name="status_filter"
                control={control}
                value={statusId}
                onChange={handleChangeStatus}
                items={statuses}
                width={210}
              />
              <TextfieldSelect
                name="course_filter"
                control={control}
                value={courseId}
                onChange={handleChangeCourse}
                items={courses}
                width={288}
              />
              <TextfieldSelect
                name="cohorts_filter"
                control={control}
                value={cohortId}
                onChange={handleChangeCohort}
                items={cohorts}
                disabled={isCohortDisabled}
                width={131}
              />
            </Box>
          </Box>
        </div>
      </form>
    </div>
  )
}

export default React.memo(AboutCompanySecondTabFilters)
