import React from 'react'
import { Typography, IconButton } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import IconClose from "../../../../assets/Icons/IconClose/IconClose"
import useStyles from './InformationPopup.styles'

export default function InformationPopup({ open, setOpen, title, subtitle, textButton }) {
  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
  }

  const onSubmit = (e) => {
    e.preventDefault()
    handleClose()
  }
  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} onSubmit={onSubmit} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {title}
                </Typography>
                <Typography variant="body1" color="textSecondary">
                  {subtitle}
                </Typography>
              </div>
            </form>
            <div className={classes.buttonContainer}>
              <Button form="form-consult" className={classes.submitButton} type="submit">
                {textButton}
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}
