import React, { useEffect, useState } from 'react'
import { useInView } from 'react-intersection-observer'
import Filters from '../../../components/Sub/Filters/Filters'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import useStyles from './CohortPage.style'
import api from '../../../api/api'
import useAsync from '../../../hooks/useAsync'
import CreateCohortPopup from '../../../components/Main/CohortPage/CreateCohortPopup/CreateCohortPopup'
import InformationPopup from '../../../components/Main/CohortPage/InformationPopup/InformationPopup'
import {
  API_COHORT_ENDPOINT,
  API_COHORT_GET_FILTERS_ENDPOINT,
  API_COHORT_BY_ID_ENDPOINT,
  CREATE_COHORT_INFORMATION_POPUP_BUTTON_TEXT,
  CREATE_COHORT_INFORMATION_POPUP_SUBTITLE,
  CREATE_COHORT_INFORMATION_POPUP_TITLE,
} from '../../../utils/constants'
import useFilters from '../../../hooks/useFilters'
import { getCohortsColumns } from '../../../utils/columns'
import createStatusesArray from '../../../utils/create-statuses-array'

const sortFieldsMapping = {
  id: 'id',
  course_id: 'course_id',
  status: 'cohort_status_id',
  cohort_number: 'cohort_number',
  start_date: 'start_date',
  end_date: 'end_date',
}

const CohortPage = ({ setSnack }) => {
  const hook = useFilters()
  const [cohorts, setCohorts] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [filtersJSON, setFiltersJSON] = useState({})
  const [newCohort, setNewCohort] = useState({})
  const [openAddCohort, setOpenAddCohort] = useState(false)
  const [openInfoPopup, setOpenInfoPopup] = useState(false)
  const [rowsCount, setRowsCount] = useState(undefined)

  // TODO        SD-1379 https://employmentdiary.atlassian.net/browse/SD-1379
  // const { ref, inView, entry } = useInView({
  //   threshold: 0.5,
  // })

  const { run } = useAsync({
    data: [],
    error: null,
  })
  const classes = useStyles()

  const getFilters = async () => {
    try {
      setIsLoading(true)
      const response = await api.get(API_COHORT_GET_FILTERS_ENDPOINT)
      setFiltersJSON(response.data)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setIsLoading(false)
    }
  }

  const getCohorts = async () => {
    try {
      setIsLoading(true)
      const response = await api.get(API_COHORT_ENDPOINT, { params: { ...hook.filters } })
      setCohorts(Object.values(response.data.results))
      // if (response.data.previous === null) {
      //   setCohorts(Object.values(response.data.results))
      // } else {
      //   setCohorts(cohorts.concat(...response.data.results))
      // }
      setRowsCount(response.data?.count)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setIsLoading(false)
    }
  }

  // useEffect(() => {
  //   if (inView && Number(hook.filters.page) < Math.ceil(rowsCount / 10)) {
  //     const page = hook.filters.page ? Number(hook.filters.page) + 1 : 1
  //     hook.changeFilters({ ...hook.filters, page })
  //   }
  // }, [inView])

  useEffect(() => {
    run(getFilters())
  }, [])

  useEffect(() => {
    if (hook.filters.start_date) run(getCohorts())
  }, [hook.filters])

  useEffect(() => {
    if (newCohort?.id) {
      run(getCohorts())
      setNewCohort({})
    }
  }, [newCohort])

  // eslint-disable-next-line no-underscore-dangle
  const _patchStatus = async (cohortId, status) => {
    try {
      setIsLoading(true)
      const response = await api.patch(API_COHORT_BY_ID_ENDPOINT(cohortId), status)
      const updatedRow = response.data
      const newCohorts = [...cohorts]
      newCohorts[newCohorts.findIndex((el) => el.id === updatedRow.id)] = updatedRow
      setCohorts(newCohorts)
      return Promise.resolve(response.data)
    } catch (e) {
      if (e?.response?.data?.status?.error === 'students exists') setOpenInfoPopup(true)
      return Promise.reject(e.response)
    } finally {
      setIsLoading(false)
    }
  }

  const patchStatus = (...args) => run(_patchStatus(...args))

  // const columns = getCohortsColumns(patchStatus, filtersJSON?.courses, createStatusesArray(filtersJSON?.statuses), ref)
  const columns = getCohortsColumns(patchStatus, filtersJSON?.courses, createStatusesArray(filtersJSON?.statuses), null)

  return (
    <main className={classes.root}>
      <CreateCohortPopup
        openState={[openAddCohort, setOpenAddCohort]}
        courses={filtersJSON?.courses}
        setSnack={setSnack}
        newCohortState={[newCohort, setNewCohort]}
      />
      <InformationPopup
        open={openInfoPopup}
        setOpen={setOpenInfoPopup}
        title={CREATE_COHORT_INFORMATION_POPUP_TITLE}
        subtitle={CREATE_COHORT_INFORMATION_POPUP_SUBTITLE}
        textButton={CREATE_COHORT_INFORMATION_POPUP_BUTTON_TEXT}
      />
      <Filters
        filterType="cohorts"
        hook={hook}
        filtersJSON={filtersJSON}
        handleAddCohort={() => setOpenAddCohort(true)}
      />
      <DataTable
        columns={columns}
        rows={cohorts}
        loading={isLoading}
        filtersHook={hook}
        rowsCount={rowsCount}
        sortFieldsMapping={sortFieldsMapping}
      />
    </main>
  )
}
export default CohortPage
