import React from 'react'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { Typography, TextField, MenuItem, IconButton, FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useStyles from './CreateCohortPopup.styles'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { newCohortSchema } from '../../../../utils/schemas'
import { API_COHORT_ENDPOINT } from '../../../../utils/constants'
import processServerError from '../../../../utils/server-error'

export default function CreateCohortPopup({ openState, courses, setSnack, newCohortState }) {
  const [open, setOpen] = openState
  const [, setNewCohort] = newCohortState
  const defaultValues = {}

  const { run } = useAsync({
    data: null,
    error: null,
  })
  const {
    control,
    handleSubmit,
    formState: { errors },
    setError,
    reset,
  } = useForm({ defaultValues, resolver: yupResolver(newCohortSchema), mode: 'onChange' })

  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
    reset(defaultValues)
  }

  const onCreateConsult = async (data) => {
    try {
      const response = await api.post(API_COHORT_ENDPOINT, { ...data })
      if (response.status === 201) {
        setNewCohort(response.data)
        reset(defaultValues)
        handleClose()
        setSnack({
          open: true,
          header: ' ',
          message: '   ',
        })
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (data) => {
    run(onCreateConsult(data))
  }

  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                   
                </Typography>
                <Typography variant="body1" color="textSecondary">
                    ,    .        
                  
                </Typography>
              </div>
              <fieldset className={classes.fieldset}>
                <div className={classes.leftColumn}>
                  <div className={classes.fieldContainer}>
                    <Typography> </Typography>
                    <Controller
                      name="course_id"
                      control={control}
                      render={({ field }) => (
                        <TextField
                          {...field}
                          placeholder="   "
                          className={classes.input}
                          variant="outlined"
                          error={errors.course_id?.message}
                          helperText={errors.course_id?.message || ' '}
                          select
                          SelectProps={{
                            MenuProps: {
                              anchorOrigin: {
                                vertical: 'bottom',
                                horizontal: 'left',
                              },
                              transformOrigin: {
                                vertical: 'top',
                                horizontal: 'left',
                              },
                              getContentAnchorEl: null,
                            },
                          }}
                        >
                          {courses.map((c) => (
                            <MenuItem className={classes.menuItem} key={c.id} value={c.id}>
                              <div className={classes.personContainer}>
                                <Typography className={classes.personField}>{c.name}</Typography>
                              </div>
                            </MenuItem>
                          ))}
                        </TextField>
                      )}
                    />
                  </div>
                  <div className={classes.fieldContainer}>
                    <Typography> </Typography>
                    <Controller
                      name="cohort_number"
                      control={control}
                      render={({ field }) => (
                        <TextField
                          {...field}
                          placeholder=" "
                          className={classes.input}
                          variant="outlined"
                          error={errors.cohort_number?.message}
                          helperText={errors.cohort_number?.message || ' '}
                        />
                      )}
                    />
                  </div>
                </div>
              </fieldset>
            </form>
            <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
              {errors.non_field_errors?.message || ' '}
            </FormHelperText>
            <div className={classes.buttonContainer}>
              <Button form="form-consult" className={classes.submitButton} type="submit">
                
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}
