/* eslint-disable no-nested-ternary */
import React from 'react'
import useStyles from './Filters.style'
import TextfieldSearch from './textfield-search/textfield-search'

function ResponsesFilters({ searchString, handleSearchChange, errors, control }) {
  const classes = useStyles()

  return (
    <form className={classes.filters__form}>
      <TextfieldSearch
        value={searchString}
        control={control}
        onChange={handleSearchChange}
        placeholder="   "
        error={!!errors.search_field?.message}
        helperText={errors.search_field?.message}
      />
    </form>
  )
}

export default React.memo(ResponsesFilters)
