/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'
import useStyles from './Filters.style'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'
import TextfieldSearch from './textfield-search/textfield-search'
import { ControlledSelectField } from '../FormElements'

function ProjectsListFilters({
  searchString,
  handleSearchChange,
  dateProps,
  errors,
  control,
  multipleCoursesProps,
  statusProps,
  hrProfileProps,
  projectsTypesProps,
}) {
  const classes = useStyles()
  const navigate = useNavigate()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { coursesOptions, selectedCourses, handleChangeMultipleCourses } = multipleCoursesProps
  const { statusId, statuses, handleChangeStatus } = statusProps
  const { hrProfileId, hrProfiles, handleChangeHrProfile } = hrProfileProps
  const { projectsTypesId, projectsTypes, handleChangeProjectsTypes } = projectsTypesProps

  // const handleDownload = () => navigate('/vacancies/export-page', { state: { vacancies: true } })

  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
               
            </Typography>
            <TextfieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder=" "
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
            />
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
          </div>

          <div className={classes.filters__buttonsContainer}>
            <div className={classes.buttonContainer}>
              {/* TODO   -  :) */}
              {/* <Button */}
              {/*  className={`${classes.button} ${classes.button_type_small} ${classes.button_color_white}`} */}
              {/*  onClick={handleDownload} */}
              {/* > */}
              {/*    */}
              {/* </Button> */}
            </div>
          </div>
        </div>

        <div className={classes.filters__container} style={{ alignItems: 'flex-start' }}>
          <div className={classes.filters} style={{ alignItems: 'flex-start' }}>
            <TextfieldSelect
              name="projects_types"
              control={control}
              // tipContent=" "
              value={projectsTypesId}
              onChange={handleChangeProjectsTypes}
              items={projectsTypes}
              width={288}
            />
            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={167}
            />
            <ControlledSelectField
              tipContent=" "
              tipBlack
              value={selectedCourses || []}
              onChange={handleChangeMultipleCourses}
              items={coursesOptions || []}
              displayEmpty
              multiple
              maxWidth={167}
              selectAll=" "
            />
            <TextfieldSelect
              name="hr_profile_filter"
              control={control}
              value={hrProfileId}
              onChange={handleChangeHrProfile}
              items={hrProfiles}
              width={288}
            />
          </div>
          <div className={classes.filters__buttonsContainer}>
            <Button
              className={`${classes.button} ${classes.button_type_very_small}`}
              onClick={() => navigate('/workshop-managers/create-project')}
            >
               
            </Button>
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(ProjectsListFilters)
